@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 1;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  margin:0;
}
body {
  font-size: 92%;
  font-family: 'Oxanium';
  color: #0f2235;
  background-image: url(/simple-react-website/static/media/1.ec243b7e.jpg);
  height: 100vh;
  background-size: cover;
  text-align: center;
 
  background-position: center;
  background-attachment: fixed;
}
h2, h3, h4, h5 {
  margin: 0 0 3px;
}




.UserCard {  
  position: relative;
  padding-bottom: 1em;
  width: 290px;
  color:black;
  /* font-family: 'Poppins', sans-serif;*/
  font-family: 'Oxanium';
  height: 360px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px black;
  margin: 10px;
  background: #0c2b5e; 
  background: -webkit-gradient(linear,right top, left top,from(#cdcdcd),to(white)); 
  background: linear-gradient(to left,#cdcdcd,white)
}

.UserCard img {

  width: 200px;
  height: 200px;
  margin: 20px 0 0 0;
  border-radius: 50%;
  border: 3px solid #0f2235;
  object-fit: cover;
}

.UserCardTop {
  height: 67%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.UserCardBottom {
  min-height: 40%;
  overflow: auto;
  min-width: 100%;
  max-width: 100%;
  padding: 0 10px 5px;
  overflow-wrap: break-word;
}

.UserCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
.UserCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
.UserCardBottom::-webkit-scrollbar-thumb {
  background-color: #bec4c8; 
  border-radius:3px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Heading {
  font-size: 150%;
}

